<template>
  <div>
    <usbanner :imgUrl="contaData.nav_img_path"></usbanner>
    <usertel :usertel="contaData" v-if="!$store.state.isMobile"></usertel>
    <div class="con-us">
      <div class="con-text">
        <p>联系我们</p>
        <p>
          400服务热线 <br />
          {{ contaData.relate_us_hot_line }}
        </p>
        <p>
          电话 <br />
          {{ contaData.relate_us_tel_phone }}
        </p>
        <p>
          邮箱 <br />
          {{ contaData.relate_us_email }}
        </p>
        <p>
          地址 <br />
          {{ contaData.relate_us_address }}
        </p>
      </div>
      <div class="com-map">
        <gdmap></gdmap>
      </div>
    </div>
    <qrcode :qrimg="qrimg"></qrcode>
  </div>
</template>
<script>
import gdmap from "@/components/gdmap";
import usbanner from "@/components/banner";
import usertel from "@/components/usertel";
import qrcode from "@/components/qrcode";
import { GET_RELATE } from "@/api/mall";
export default {
  name: "contactus",
  components: { gdmap, usbanner, usertel, qrcode },
  data() {
    return {
      contaData: {},
      qrimg: []
    };
  },
  mounted() {
    this.GaodeMap();
    this.loadData();
  },
  methods: {
    loadData() {
      GET_RELATE({
        type: this.$store.state.isMobile
      })
        .then(res => {
          this.contaData = res.data;
          this.qrimg = res.data.qrcode_list;
        })
        .catch(err => {});
    },
    GaodeMap() {
      var map = new AMap.Map("all-map", {
        center: [113.405999, 23.092129], // [纬度，经度]
        resizeEnable: true,
        zoom: 18
      });
      var marker = new AMap.Marker({
        position: map.getCenter(),
        icon: require("../../../static/images/location.png"),
        offset: new AMap.Pixel(0, 0),
        // 设置是否可拖拽
        draggable: false,
        cursor: "move",
        zoomToAccuracy: true
      });

      marker.setMap(map);

      // 设置点标记的动画效果，此处为弹跳效果
      // marker.setAnimation('AMAP_ANIMATION_BOUNCE');
    }
  }
};
</script>
<style lang="scss" scoped>
.con-us {
  display: flex;
  width: 62.5%;
  margin: 0 auto;
  padding: 0 100px;
  justify-content: space-between;
  padding-bottom: 160px;
  .con-text {
    text-align: left;
    padding-top: 20px;
    p {
      line-height: 22px;
      font-size: 14px;
      color: #666666;
      padding-bottom: 36px;
      &:nth-child(1) {
        font-size: 16px;
        color: #b81d2d;
        font-weight: bold;
      }
    }
  }
  .com-map {
    width: 50%;
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .con-us {
    display: block;
    width: 95%;
    margin: 0 auto;
    padding: 0;
    margin-top: 20px;
    // justify-content: space-between;
    padding-bottom: 20px;
    .con-text {
      text-align: left;
      padding-top: 10px;
      margin-bottom: 30px;
      p {
        line-height: 22px;
        font-size: 14px;
        color: #666666;
        padding-bottom: 10px;
        &:nth-child(1) {
          font-size: 16px;
          color: #b81d2d;
          font-weight: bold;
        }
      }
    }
    .com-map {
      width: 100%;
    }
  }
}
</style>



