<template>
  <div class="user-tel">
    <ul>
      <li>
        <div>
          <span class="iconfont logo">&#xe6ce;</span>
        </div>
        <div>
          <p>{{ usertel.member_first_spec_name }}</p>
          <p>{{ usertel.member_first_spec_relate }}</p>
        </div>
      </li>
      <li>
        <div>
          <span class="iconfont logo">&#xe6cd;</span>
        </div>
        <div>
          <p>{{ usertel.member_second_spec_name }}</p>
          <p>{{ usertel.member_second_spec_relate }}</p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  props: {
    usertel: {
      type: Object
    }
  }
};
</script>
<style lang="scss" scoped>
.user-tel {
  ul {
    display: flex;
    justify-content: space-between;
    width: 73%;
    margin: 0 auto;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 3px 35px 0px rgba(0, 0, 0, 0.03);
    border-radius: 20px;
    padding: 70px 0;
    position: relative;
    top: -90px;
    li {
      display: flex;
      justify-content: space-between;
      width: 50%;
      justify-content: center;
      align-items: center;
      p {
        text-align: left;
        font-weight: bold;
        color: #666666;
        font-size: 16px;
        padding: 8px 15px;
      }
      .logo {
        font-size: 48px;
        color: #b81d2d;
      }
    }
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .user-tel {
    ul {
      display: flex;
      justify-content: space-between;
      width: 95%;
      margin: 0 auto;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 3px 35px 0px rgba(0, 0, 0, 0.03);
      border-radius: 0;
      padding: 0;
      position: relative;
      padding: 0 2.5%;
      top: 20px;
      li {
        display: flex;
        justify-content: flex-start;
        width: 100%;
        margin-bottom: 10px;
        align-items: center;
        p {
          text-align: left;
          font-weight: bold;
          color: #666666;
          font-size: 16px;
          padding: 8px 2px;
        }
        .logo {
          font-size: 48px;
          color: #b81d2d;
        }
      }
    }
  }
}
</style>



