<template>
  <ul class="qrcode">
    <li v-for="(item, index) in qrimg" :key="index" v-if="item.img_path">
      <img :src="item.img_path" alt="" />
      <p>{{ item.title }}</p>
    </li>
  </ul>
</template>
<script>
export default {
  name: "qrcode",
  props: {
    qrimg: {
      type: Array
    }
  }
};
</script>
<style lang="scss" scoped>
.qrcode {
  background: #f9f9f9;

  display: flex;
  justify-content: center;
  li {
    padding: 70px 0;
    background: white;
    width: 170px;
    text-align: center;
    margin: 0 70px;
    // img {
    // }
    p {
      font-size: 14px;
      color: #000000;
      line-height: 22px;
      padding: 12px 0;
    }
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .qrcode {
    background: #f9f9f9;
    display: flex;
    justify-content: center;
    li {
      padding: 20px 0;
      background: white;
      width: 30%;
      text-align: center;
      margin: 0;
      padding: 10px 0 0 0;
      // img {
      // }
      p {
        font-size: 14px;
        color: #000000;
        line-height: 22px;
        padding: 12px 0;
      }
    }
  }
}
</style>



