<template>
  <div class="banner">
    <el-image :src="imgUrl"
              fit="cover"></el-image>
  </div>
</template>
<script>
export default {
  name: 'banner',
  props: {
    imgUrl: {
      type: String,
      default: require('../../static/images/bg_contacus.png'),
    },
  },
};
</script>
<style lang="scss" scoped>
.banner {
  img {
    width: 100%;
    height: 100%;
  }
}
</style>





